#StartUp{
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: space-between; */
    /* flex-wrap: wrap; */
}
.StartUp__left-content{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    /* align-items: center; */
}
.StartUp__left-content div{
    padding: 20px;
    border-radius: 10px;
    background: #424b6e;
    font-family:monospace;
    font-size: 25px;
    box-shadow: 20px 20px 10px grey;
    margin-bottom: 50px;
    width: fit-content;
}

.StartUp__left-content div:nth-child(odd){
    transform: rotate(-3deg);
}

.StartUp__left-content div:nth-child(even){
    transform: rotate(3deg);
    
}


